.sawo-form {
  display: flex;
  justify-content: center;
  align-items: center;
}
#sawo-container {
  padding: 20px;
}

.centered {
  display: grid;
  min-height: 100vh;
  place-items: center;
}
.animated-title {
  padding: 10px;
  position: relative;
  font-family: var(--font1), sans-serif;
  font-size: 4em;
  font-weight: bold;
  overflow: hidden;
  border-radius: 4px;
  background: RGB(55, 168, 86);
}
.animated-title:before {
  padding: 10px;
  position: absolute;
  content: '';
  inset: 0;
  background: gold;
  animation: circle 2s infinite ease-out;
  clip-path: circle(0%);
}
.animated-title:after {
  padding: 10px;
  position: absolute;
  content: var(--text);
  inset: 0;
  color: white;
  animation: circle 2s infinite;
  clip-path: circle(0%);
}
@keyframes circle {
  0% {
    clip-path: circle(0%);
  }
  100% {
    clip-path: circle(100%);
  }
}
